import React from 'react'
import { graphql } from 'gatsby'
// import Img from 'gatsby-image'
// import link from 'gatsby'
import Layout from '../components/layout'
import ReactMarkdown from 'react-markdown/with-html'
import ImagePointer from '../components/imagePointer.js'

import '../components/article.css'

const ArticleTemplate = ({ data }) => (
  <Layout>
    <div className="art-background">
      <ImagePointer imageFluid={ data.strapiArticle.thumbnail.childImageSharp.fluid } />
      <h1>{data.strapiArticle.title}</h1>
      <ReactMarkdown source={data.strapiArticle.content} className="art-main" escapeHtml={false} transformImageUri={uri => uri.startsWith('http') ? uri : `${process.env.IMAGE_BASE_URL}${uri}`}/>
    </div>
  </Layout>
)

// This should go inside className = "art-background"
// <Img className="art-mainimg" fluid={data.strapiArticle.thumbnail.childImageSharp.fluid} />

export default ArticleTemplate

export const query = graphql`
  query ArticleTemplate($id: String!) {
    strapiArticle(id: {eq: $id}) {
      id
      title
      content
      thumbnail {
          childImageSharp {
            fluid(maxWidth: 960) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      author {
        id
        username
      }
    }
  }
`
